  color: #292C32;
  font-family: Montserrat;
  font-size: 15px;
  font-style: normal;
  font-weight: 700;
  line-height: 19px;
}
.teacher-stat-grade__icons {
  padding: 21px 0 0 3px;
  display: flex;
  flex-wrap: wrap;
  gap: 31px;
