  color: #8B919E;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.3;
  margin: 0 0 5px;
}
.add-selects__select-list {
  display: flex;
  align-items: center;
}
.add-selects__btn {
  display: block;
  margin: 0 0 0 5px;
  flex: 0 0 20px;
  height: 20px;
  border: 1px solid rgba(240, 73, 115, 0.5);
  border-radius: 50%;
  background: #fff;
  position: relative;
}
.add-selects__btn::after, .add-selects__btn::before {
  content: "";
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 2px;
  background: rgb(240, 73, 115);
  transform: translate(-50%, -50%);
}
.add-selects__btn::after {
  transform: translate(-50%, -50%) rotate(90deg);
}
.add-selects__btn:hover {
  border-color: rgb(240, 73, 115);
